.NavBar {
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 0.5em ;
  color: white;
  background-color: #141127;
  justify-content: space-between;
}

.NavBar .searchbox {
  width: 20em;
}

.NavBar .searchbox .ms-SearchBox {
  margin-bottom: 0;
  background-color: white;
}
.body .container {
  padding: 1em;
}

.body .ms-Breadcrumb {
  margin-top: 0;
  margin-bottom: 1em;
}

.body .selection {
  height: calc(100vh - 16.5em);
  overflow-x: auto;
}

.body .selection .selection-item {
  display: flex;
  padding: 0.5em;
}

.body .selection .selection-item .name {
  margin-left: 1em;
}

.footer {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #141127;  
}

.footer-container {
  color: white;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  font-size: 16px;
}

/** Main Layout rules */
.App {
  min-height: 100vh; 
}

.App {
  display: flex;
  flex-direction: column;
  flex: 1 1;
}

.App .body {
  display: flex;
  flex-direction: row;
  flex: 1 1;
}

.body .sidebar {
  order: -1;
}

.body .content {
  flex: 1 1;
}

.body .sidebar {
  max-width: 12em;
  flex: 0 0 12em;
}

.App .header {
  height: 4em;
}

.App .footer {
  height: 4em;
}

/** Text */
.text-red {
  color: red;
}
